.employers {
    padding: 137px 0 150px;

    .employers__container {

        .employers__body {
            display: flex;
            justify-content: space-between;

            .employers__carousel {
                width: 625/1280 * 100%;
                position: relative;
                
                .employers__images {
                    display: flex;
                    overflow: hidden;
                    column-gap: 31/625 * 100%;
                    aspect-ratio: 1;
                    margin-bottom: 30px;
                    position: relative;
                    z-index: 1;

                    &.first-position {

                        img:first-child {
                            opacity: 1;
                        }
                    }

                    &.second-position {

                        img:first-child {
                            margin-left: -627px;
                        }

                        img:nth-child(2) {
                            opacity: 1;
                        }
                    }

                    &.third-position {

                        img:first-child {
                            margin-left: -1253px;
                        }

                        img:nth-child(3) {
                            opacity: 1;
                        }
                    }

                    &.fourth-position {

                        img:first-child {
                            margin-left: -1879px;
                        }

                        img:nth-child(4) {
                            opacity: 1;
                        }
                    }

                    &.fifth-position {

                        img:first-child {
                            margin-left: -2505px;
                        }

                        img:nth-child(5) {
                            opacity: 1;
                        }
                    }

                    img {
                        width: 596/625 * 100%;
                        aspect-ratio: 1;
                        margin-bottom: 30px;
                        position: relative;
                        transition: all 0.5s ease;
                        opacity: 0;
                    }
                }

                @media (max-width: 1309px) {
                    .employers__images {
    
                        &.second-position {
    
                            img:first-child {
                                margin-left: -99%;
                            }
                        }
    
                        &.third-position {
    
                            img:first-child {
                                margin-left: -198%;
                            }
                        }
    
                        &.fourth-position {
    
                            img:first-child {
                                margin-left: -297%;
                            }
                        }
    
                        &.fifth-position {
    
                            img:first-child {
                                margin-left: -403%;
                            }
                        }
                    }
                }

                .employers__carousel__substrate {
                    position: absolute;
                    top: 29/625 * 100%;
                    right: 0;
                    width: 596/625 * 100%;
                    aspect-ratio: 1;
                    background-color: #363636;
                }

                .switcher-employers {
                    display: flex;
                    align-items: center;
                    column-gap: 128px;
        
                    .switcher-employers__arrows {
                        display: flex;
                        column-gap: 5px;
        
                        .switcher-employers__arrow {
                            cursor: pointer;
        
                            &:last-child {
                                transform: rotate(180deg);
                            }
        
                            &.active {
                                g {
                                    opacity: 1;
                                }
                                rect {
                                    fill: $accent-color;
                                }
                            }
                        }
                    }
                    
                    .switcher-employers__scale {
                        width: 407px;
                        height: 2px;
                        display: flex;
        
                        .switcher-employers__scale__item {
                            background-color: $secondary-text-color;
                            width: 20%;
                            height: 100%;
                            cursor: pointer;
        
                            &.active {
                                background-color: $accent-color;
                            }
                        }
                    }

                    @media (max-width: 792px) {
                        .switcher-employers__scale {
                            width: 100%;
                        }
                    }
                }

                @media (max-width: 792px) {
                    .switcher-employers {
                        column-gap: 32/280 * 100%;
                    }
                }
            }

            @media (max-width: 792px) {
                .employers__carousel {
                    width: 100%;
                }
            }

            .employers__text {
                width: 625/1280 * 100%;
                padding: 45px 0 60px;

                h2 {
                    font-size: 3rem;
                    line-height: 35/30 * 100%;
                    font-weight: 700;
                    color: white;
                    margin-bottom: 255px;
                }

                @media (max-width: 1164px) {
                    h2 {
                        margin-bottom: 166px;
                    }
                }

                @media (max-width: 983px) {
                    h2 {
                        font-size: 2.4rem;
                        margin-bottom: 125px;
                    }
                }

                @media (max-width: 824px) {
                    h2 {
                        font-size: 2.4rem;
                        margin-bottom: 81px;
                    }
                }

                @media (max-width: 792px) {
                    h2 {
                        font-size: 1.8rem;
                        margin-bottom: 60px;
                        text-align: center;
                    }
                }


                .employers__description {

                    & > *:not(:last-child) {
                        margin-bottom: 13px;
                    }

                    p {
                        font-size: 2rem;
                        line-height: 150%;
                        font-weight: normal;
                        color: $secondary-text-color;
                    }

                    @media (max-width: 869px) {
                        p {
                            font-size: 1.8rem;
                        }
                    }

                    @media (max-width: 792px) {
                        p {
                            font-size: 1.6rem;
                        }
                    }

                    p.employers__employer {
                        font-weight: 700;
                        font-size: 2.4rem;
                        line-height: 28/24 * 100%;
                        color: white;
                    }

                    @media (maax-width: 869px) {
                        p.employers__employer {
                            font-size: 2rem;
                        }
                    }

                    @media (maax-width: 869px) {
                        p.employers__employer {
                            font-size: 1.6rem;
                        }
                    }
                }

                @media (max-width: 792px) {
                    .employers__description {
                        margin-bottom: 20px;
                    } 
                }
            }

            @media (max-width: 792px) {
                .employers__text {
                    padding: 0;
                    width: 100%;
                }
            }
        }

        @media (max-width: 792px) {
            .employers__body {
                flex-direction: column-reverse;
                align-items: center;
            }
        }
    }
}

@media (max-width: 792px) {
    .employers {
        padding: 50px 0;
    }
}